/* Testimonials */

.s-testimonials {
  padding: 140px 0 125px;
  @include xs() {
    padding: 20px 0 15px; }
  &__content {
    margin-left: 30px;
    margin-right: 30px; }
  &__title {
    @extend %section-header; }
  &__text {
    margin-bottom: 60px;
    @include xs() {
      margin-bottom: 10px; } }

  /* Slick slider dots customize */
  .slick-dots li button {
    @include size(16px);
    @include round();
    background-color: $dark-color;
    &:before {
      content: ''; }
    &:hover {
      background-color:  $primary-color; } }
  .slick-dots li.slick-active button {
      background-color:  $primary-color; }

  &__arrow {
    @include reset-btn();
    position: absolute;
    top: 64%;
    @include xs() {
      top: 53%; }
    width: 30px;
    height: 50px;
    opacity: 0.3;
    @include transition(opacity);
    &:hover {
      opacity: 1; } }
  &__arrow--prev {
    left: 4px; }
  &__arrow--next {
    right: 4px; } }

.testimonial {
  &__avatar {
    position: relative;
    @include size(170px);
    margin: 10px auto;
    img {
      @include round(); } }
  &__social {
    @include position(absolute, 22px -5px null null);
    @include round();
    @include size(38px);
    background-color: #4267b2; }
  &__name {
    font: bold 17.5px $font-second;
    color: $dark-color;
    text-transform: uppercase;
    margin: 33px 0 15px; }
  &__text {
    margin: 17px 130px 20px;
    @include md() {
      margin: 17px 70px 20px; }
    @include sd() {
      margin: 17px 30px 20px; }
    @include xs() {
      margin: 10px 0 15px;
      line-height: 1.35; } } }

